.l-header {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 100;
}

.l-header__bar {
  display: none;
  position: fixed;
  z-index: 100;
  width: calc(100% - 59px);
  height: 59px;
  justify-content: flex-start;
  align-items: center;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.2);
}
.l-header__bar picture {
  margin-left: 16px;
  margin-top: -1px;
}

.l-header-buttons {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 100;
}

@media screen and (max-width: 767px) {
  .l-header {
    left: 0;
    top: 0;
  }
  .l-header__bar {
    display: flex;
    position: fixed;
  }
  .l-header-buttons .p-hamburger__wrapper {
    z-index: 200;
